window.jQuery = function(selectorOrArray){//selector
    let elements
    if(typeof selectorOrArray === 'string'){
        elements = document.querySelectorAll(selectorOrArray)
    }
    if(selectorOrArray instanceof Array){
        elements = selectorOrArray
    }
    // const  elements = document.querySelectorAll(selectorOrArray)
    // let elements = document.querySelectorAll(selectorOrArray)
    // return elements // ‰∏ç return elements
    
    //apiÂèØ‰ª•Êìç‰Ωúelements
    // const api = {
    //     // Èó≠ÂåÖ ÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÁöÑÂèòÈáè ÂáΩÊï∞‰∏çÊ≠ªelements‰∏çÊ≠ª
    //     addClass(className){
    //         for(let i = 0; i < elements.length; i++){
    //             elements[i].classList.add(className)
    //         }
    //         // return undefined
    //         // return api
    //         return this
    //     }
    // }
    // return api 

    return {
        addClass(className){
            for(let i = 0; i < elements.length; i++){
                elements[i].classList.add(className)
            }
            return this
        },
        find(selector){
             let array = []
             for(let i = 0; i < elements.length; i++){
                 const e = Array.from(elements[i].querySelectorAll(selector))
                 array = array.concat(e)
             }
            //  return array //ÈìæÂºèÊìç‰ΩúÊ≤°Êúâ‰∫Ü
            //  return this // ‰∏çËÉΩreturn this Êìç‰ΩúÁöÑ‰∏çÊòØelements‰∫Ü

            //Êîπelements Êúâbug
            // elements = array
            // return this

            // ËÆ©jQueryÊé•Âèó‰∏Ä‰∏™Êï∞ÁªÑ ËøîÂõû‰∏Ä‰∏™Êñ∞Api
            // const newApi = jQuery(array)
            // return newApi 
            // return jQuery(array)

            array.oldApi = this // thisÊòØ Êóßapi
            return jQuery(array)
        },
        oldApi: selectorOrArray.oldApi,
        end(){
            return this.oldApi // thisÊòØ Êñ∞Api
        },
        each(fn){
            for(let i = 0; i < elements.length; i++){
                fn.call(null, elements[i], i)
            }
            return this
        },
        parent(){//Ëé∑ÂèñÊØè‰∏™ÂÖÉÁ¥†ÁöÑÁà∏Áà∏
            const array = []
            this.each(node => {
                if(array.indexOf(node.parentNode) === -1){
                    array.push(node.parentNode)
                }
            })
            return jQuery(array)
        },
        children(){
            const array = []
            this.each(node => {
                array.push(...node.children) //Â±ïÂºÄÊìç‰ΩúÁ¨¶ Â∞Ünode.childrenÊï∞ÁªÑÊãÜÂºÄ
            })
            return jQuery(array)
        },
        prints(){
            console.log(elements)
        }
    }
}

window.$ = window.jQuery
